"use client";
import { useEffect } from "react";

export const Timer = ({ onTick }: { onTick: () => void }) => {
  useEffect(() => {
    const interval = setInterval(onTick, 1000);
    return () => clearInterval(interval);
  }, [onTick]);

  return (
    <div className="fixed top-4 right-4 bg-white px-3 py-1 rounded-lg shadow text-indigo-700 font-semibold">
      ⏱️ זמן ריצה...
    </div>
  );
};
